@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
:root {
  --primary: rgb(6, 214, 160);
  --secondary: rgb(250, 100, 61);
}

html,
body {
  height: 100%;
  overflow-x: hidden;
  font-family: "Open Sans";
  letter-spacing: -0.03em;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="date"],
input[type="number"] {
  font-size: 15px;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="date"],
input[type="number"],
input[type="text"]:disabled,
input[type="email"]:disabled,
input[type="password"]:disabled,
input[type="date"]:disabled,
input[type="number"]:disabled {
  background: #f6f5f5;
  border: 0.5px solid #dcdcdc;
  outline: none;
  color: #626264;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.bg-primary {
  background-color: rgb(6, 214, 160) !important;
  background-color: var(--primary) !important;
}

.bg-secondary {
  background-color: rgb(250, 100, 61) !important;
  background-color: var(--secondary) !important;
}

.btn {
  font-size: 15px;
}

.btn-primary,
.btn-primary:focus,
.btn-primary:active,
.btn-primary:disabled,
.btn-primary:hover {
  background-color: rgb(6, 214, 160) !important;
  background-color: var(--primary) !important;
  border-color: rgb(6, 214, 160);
  border-color: var(--primary);
}

.btn-secondary,
.btn-secondary:focus,
.btn-secondary:active,
.btn-secondary:disabled,
.btn-secondary:hover {
  background-color: rgb(250, 100, 61) !important;
  background-color: var(--secondary) !important;
  border-color: rgb(250, 100, 61);
  border-color: var(--secondary);
}

.badge {
  font-weight: lighter;
}

.badge.badge-primary {
  background: rgba(6, 214, 160, 0.1);
  color: rgb(6, 214, 160);
  color: var(--primary);
}

.badge.badge-secondary {
  background: rgba(250, 100, 61, 0.1);
  color: rgb(250, 100, 61);
  color: var(--secondary);
}

.box {
  background: #fff;
  box-shadow: 0px 0px 11px 6px rgba(0, 0, 0, 0.08);
  border-radius: 5px;
  display: inline-block;
  padding: 50px;
}

.sidebar h5 {
  height: 30px;
  margin-bottom: 0;
}

.sidebar .products {
  height: calc(100% - 190px);
  overflow: auto;
  -webkit-align-content: flex-start;
          align-content: flex-start;
}

.sidebar .footer {
  height: 160px;
  background-color: #f6f5f5;
}

.product-list {
  margin-top: 10px;
}

.product-list .badge {
  margin-bottom: 0;
}

.product-list img {
  border: 1px solid #dcdcdcdc;
  border-radius: 5px;
  padding: 10px;
}

.product-list small {
  margin-top: 0;
  line-height: 15px;
  display: block;
}

.product-list button {
  position: absolute;
  right: 30px;
  padding: 1px 14px;
  font-size: 25px;
  margin-top: 20px;
}

.cart-button {
  position: relative;
  float: right;
  z-index: 999;
  margin-top: -70px;
  margin-right: 20px;
}

.section-title {
  border-bottom: 3px solid var(--primary);
  padding-bottom: 7px;
  margin: 20px 0;
  display: inline-block;
}

.box-sidebar {
  max-height: 500px;
  overflow-y: auto;
}

.petshop-infos span {
  vertical-align: middle;
  color: #626264;
}

.petshop-infos span.mdi-star {
  color: #facd5d;
}

.petshop-infos text {
  font-size: 13px;
  margin-right: 10px;
  vertical-align: middle;
  color: #626264;
}

.petshop-image {
  border-radius: 5px;
  margin-bottom: 20px;
}

.product button.btn-secondary {
  padding: 1px 14px;
}

.product {
  margin-bottom: 50px;
}

.product img {
  border: 1px solid #dcdcdc;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 20px;
  max-height: 210px;
  width: 100%;
}

.product .badge {
  margin-bottom: 0;
}

.product small {
  margin-top: 0;
  line-height: 15px;
  display: block;
}

.product button {
  position: absolute;
  right: 30px;
  font-size: 25px;
  margin-top: 155px;
  padding: 1px 12px;
}

.petshop-list-container {
  height: 300px;
  position: absolute;
  margin-bottom: 0;
  top: 91px;
  z-index: 999;
  left: 0;
  right: 0;

  background: linear-gradient(
    180deg,
    #ffffff 55.47%,
    rgba(255, 255, 255, 0) 100%
  );
}

/* width */
::-webkit-scrollbar {
  width: 5px;
  height: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--primary);
  border-radius: 50px;
}

.petshop-list {
  padding: 15px;
  white-space: nowrap;
  overflow-x: auto;
  width: 100%;
  margin-top: 15px;
}

.petshop {
  list-style: none;
  padding: 7px;
  margin-right: 5px;
  border-radius: 5px;
  border-bottom: 3px solid transparent;
  transition: 0.5s;
}

.petshop.active,
.petshop:hover {
  cursor: pointer;
  border-bottom: 3px solid var(--primary);
  transition: 0.5s;
  background-color: #f6f5f5;
}

.petshop img {
  width: 80px;
  border-radius: 5px;
  vertical-align: baseline;
}

.petshop b {
  margin-bottom: 0;
}

.petshop-infos span {
  vertical-align: middle;
  color: #626264;
}

.petshop-infos span.mdi-star {
  color: #facd5d;
}

.petshop-infos text {
  font-size: 13px;
  margin-right: 10px;
  vertical-align: middle;
  color: #626264;
}

.container-map {
  height: calc(100% - 94px);
}

.img-marker {
  width: 48px;
  position: absolute;
  top: 15px;
  left: 23.4px;
  border-radius: 100%;
}

